import React from 'react';
import PropTypes from 'prop-types';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import {
    Box,
    TextField,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    FormControlLabel,
    Switch,
    Button,
    Grid,
    Typography,
} from '@mui/material';
import {
    Code as CodeIcon,
    Description as DescriptionIcon
} from '@mui/icons-material';
import CodeEditor from '../../../../components/CodeEditor';

const validationSchema = Yup.object({
    title: Yup.string()
        .min(3, 'Title must be at least 3 characters')
        .max(200, 'Title must be less than 200 characters')
        .required('Title is required'),
    description: Yup.string()
        .min(10, 'Description must be at least 10 characters')
        .max(1000, 'Description must be less than 1000 characters')
        .required('Description is required'),
    project_type: Yup.string()
        .oneOf(['code', 'text'], 'Invalid project type')
        .required('Project type is required'),
    language: Yup.string()
        .when('project_type', {
            is: 'code',
            then: (schema) => schema.required('Language is required for code projects'),
            otherwise: (schema) => schema.nullable()
        }),
    price: Yup.number()
        .min(0, 'Price must be non-negative')
        .max(9999.99, 'Price must be less than $10,000'),
    price_per_use: Yup.number()
        .min(0, 'Price per use must be non-negative')
        .max(999.99, 'Price per use must be less than $1,000'),
    is_public: Yup.boolean(),
    content: Yup.string()
        .min(1, 'Content is required')
        .required('Content is required')
});

// Common styling for form components
const formFieldStyles = {
    '& .MuiInputBase-root': {
        color: 'black',
        '& fieldset': {
            borderColor: 'black'
        },
        '&:hover fieldset': {
            borderColor: 'black'
        },
        '&.Mui-focused fieldset': {
            borderColor: 'black'
        }
    },
    '& .MuiInputLabel-root': {
        color: 'black',
        '&.Mui-focused': {
            color: 'black'
        }
    },
    '& .MuiFormHelperText-root': {
        color: 'black'
    }
};

const ProjectForm = ({ project, onSubmit, onCancel }) => {

    const formik = useFormik({
        initialValues: {
            title: project?.title || '',
            description: project?.description || '',
            project_type: project?.project_type || 'code',
            language: project?.language || 'javascript',
            price: project?.price || 0,
            price_per_use: project?.price_per_use || 0.10,
            is_public: project?.is_public || false,
            content: project?.content || '',
            usage_instructions: project?.usage_instructions || ''
        },
        validationSchema,
        onSubmit: (values) => {
            onSubmit(values);
        }
    });



    const getProjectTypeIcon = (type) => {
        switch (type) {
            case 'code':
                return <CodeIcon />;
            case 'text':
                return <DescriptionIcon />;
            default:
                return <CodeIcon />;
        }
    };

    // Available programming languages
    const programmingLanguages = [
        { value: 'javascript', label: 'JavaScript' },
        { value: 'python', label: 'Python' },
        { value: 'java', label: 'Java' },
        { value: 'php', label: 'PHP' },
        { value: 'go', label: 'Go' },
        { value: 'rust', label: 'Rust' },
        { value: 'sql', label: 'SQL' }
    ];

    return (
        <Box component="form" onSubmit={formik.handleSubmit} sx={{ mt: 2 }}>
            <Grid container spacing={3}>
                {/* Basic Information */}
                <Grid item xs={12}>
                    <Typography variant="h6" gutterBottom color="black">
                        Basic Information
                    </Typography>
                </Grid>

                <Grid item xs={12} md={8}>
                    <TextField
                        fullWidth
                        name="title"
                        label="Project Title"
                        value={formik.values.title}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        error={
                            formik.touched.title && 
                            Boolean(formik.errors.title)
                        }
                        helperText={
                            formik.touched.title && 
                            formik.errors.title
                        }
                        sx={formFieldStyles}
                    />
                </Grid>

                <Grid item xs={12} md={4}>
                    <FormControl fullWidth sx={formFieldStyles}>
                        <InputLabel>Project Type</InputLabel>
                        <Select
                            name="project_type"
                            value={formik.values.project_type}
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                            error={
                                formik.touched.project_type && 
                                Boolean(formik.errors.project_type)
                            }
                            label="Project Type"
                        >
                            <MenuItem value="code">
                                <Box 
                                    sx={{ 
                                        display: 'flex', 
                                        alignItems: 'center', 
                                        gap: 1,
                                        color: 'black'
                                    }}
                                >
                                    <CodeIcon fontSize="small" />
                                    Code Project
                                </Box>
                            </MenuItem>
                            <MenuItem value="text">
                                <Box 
                                    sx={{ 
                                        display: 'flex', 
                                        alignItems: 'center', 
                                        gap: 1,
                                        color: 'black'
                                    }}
                                >
                                    <DescriptionIcon fontSize="small" />
                                    Text Project
                                </Box>
                            </MenuItem>
                        </Select>
                    </FormControl>
                </Grid>

                {/* Language Selector for Code Projects */}
                {formik.values.project_type === 'code' && (
                    <Grid item xs={12} md={6}>
                        <FormControl fullWidth sx={formFieldStyles}>
                            <InputLabel>Programming Language</InputLabel>
                            <Select
                                name="language"
                                value={formik.values.language}
                                onChange={formik.handleChange}
                                onBlur={formik.handleBlur}
                                error={
                                    formik.touched.language && 
                                    Boolean(formik.errors.language)
                                }
                                label="Programming Language"
                            >
                                {programmingLanguages.map((lang) => (
                                    <MenuItem
                                        key={lang.value}
                                        value={lang.value}
                                        sx={{ color: 'black !important',
                                            '&:hover, &:active, &.active, &.Mui-selected': {
                                                color: 'black !important'
                                            }
                                        }}
                                    >
                                        {lang.label}
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                    </Grid>
                )}

                <Grid item xs={12}>
                    <TextField
                        fullWidth
                        multiline
                        rows={3}
                        name="description"
                        label="Description"
                        value={formik.values.description}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        error={
                            formik.touched.description && 
                            Boolean(formik.errors.description)
                        }
                        helperText={
                            formik.touched.description && 
                            formik.errors.description
                        }
                        sx={formFieldStyles}
                    />
                </Grid>

                {/* Pricing */}
                <Grid item xs={12}>
                    <Typography variant="h6" gutterBottom color="black">
                        Pricing
                    </Typography>
                </Grid>

                <Grid item xs={12} md={6}>
                    <TextField
                        fullWidth
                        type="number"
                        name="price"
                        label="One-time Purchase Price ($)"
                        value={formik.values.price}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        error={
                            formik.touched.price && 
                            Boolean(formik.errors.price)
                        }
                        helperText={
                            formik.touched.price && 
                            formik.errors.price
                        }
                        inputProps={{ min: 0, step: 0.01 }}
                        sx={formFieldStyles}
                    />
                </Grid>

                <Grid item xs={12} md={6}>
                    <TextField
                        fullWidth
                        type="number"
                        name="price_per_use"
                        label="Price per Use ($)"
                        value={formik.values.price_per_use}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        error={
                            formik.touched.price_per_use && 
                            Boolean(formik.errors.price_per_use)
                        }
                        helperText={
                            formik.touched.price_per_use && 
                            formik.errors.price_per_use
                        }
                        inputProps={{ min: 0, step: 0.01 }}
                        sx={formFieldStyles}
                    />
                </Grid>

                <Grid item xs={12}>
                    <FormControlLabel
                        control={
                            <Switch
                                name="is_public"
                                checked={formik.values.is_public}
                                onChange={formik.handleChange}
                                sx={{
                                    '& .MuiSwitch-switchBase.Mui-checked': {
                                        color: 'black',
                                        '&:hover': {
                                            backgroundColor: 'rgba(0, 0, 0, 0.08)'
                                        }
                                    },
                                    '& .MuiSwitch-switchBase.Mui-checked + .MuiSwitch-track': {
                                        backgroundColor: 'black'
                                    }
                                }}
                            />
                        }
                        label="Make this project public"
                        sx={{ color: 'black' }}
                    />
                </Grid>



                {/* Content */}
                <Grid item xs={12}>
                    <Typography variant="h6" gutterBottom color="black">
                        Project Content
                    </Typography>
                </Grid>

                <Grid item xs={12}>
                    <Box sx={{ height: '300px', width: '100%' }}>
                        <CodeEditor
                            value={formik.values.content}
                            onChange={(content) => formik.setFieldValue('content', content)}
                            language={formik.values.project_type === 'code' ? formik.values.language : 'javascript'}
                        />
                        {formik.touched.content && Boolean(formik.errors.content) && (
                            <Typography variant="body2" color="error">
                                {formik.errors.content}
                            </Typography>
                        )}
                    </Box>
                </Grid>

                <Grid item xs={12}>
                    <TextField
                        fullWidth
                        multiline
                        rows={3}
                        name="usage_instructions"
                        label="Usage Instructions"
                        value={formik.values.usage_instructions}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        placeholder="Explain how to use this project..."
                        sx={formFieldStyles}
                    />
                </Grid>



                {/* Action Buttons */}
                <Grid item xs={12}>
                    <Box 
                        sx={{ 
                            display: 'flex', 
                            gap: 2, 
                            justifyContent: 'flex-end' 
                        }}
                    >
                        {onCancel && (
                            <Button 
                                variant="outlined" 
                                onClick={onCancel}
                                sx={{ 
                                    color: 'black', 
                                    borderColor: 'black',
                                    '&:hover': {
                                        borderColor: 'black',
                                        backgroundColor: 'rgba(0, 0, 0, 0.04)'
                                    }
                                }}
                            >
                                Cancel
                            </Button>
                        )}
                        <Button
                            type="submit"
                            variant="contained"
                            disabled={formik.isSubmitting}
                            sx={{ 
                                backgroundColor: 'black',
                                color: 'white',
                                '&:hover': {
                                    backgroundColor: 'rgba(0, 0, 0, 0.8)'
                                }
                            }}
                        >
                            {project ? 'Update Project' : 'Create Project'}
                        </Button>
                    </Box>
                </Grid>
            </Grid>
        </Box>
    );
};

ProjectForm.propTypes = {
    project: PropTypes.shape({
        title: PropTypes.string,
        description: PropTypes.string,
        project_type: PropTypes.oneOf(['code', 'text']),
        language: PropTypes.string,
        price: PropTypes.number,
        price_per_use: PropTypes.number,
        is_public: PropTypes.bool,
        content: PropTypes.string,
        usage_instructions: PropTypes.string
    }),
    onSubmit: PropTypes.func.isRequired,
    onCancel: PropTypes.func
};

export default ProjectForm; 