import axios from 'axios';
import Cookies from 'js-cookie';

const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:8000';

// Function to fetch CSRF token if needed
const fetchCSRFToken = async () => {
  try {
    // Check if CSRF token already exists
    if (!Cookies.get('csrftoken')) {
      console.log('No CSRF token found, fetching a new one...');
      const response = await axios.get(`${API_BASE_URL}/api/get_csrf_token/`, {
        withCredentials: true
      });
      if (response.status === 200) {
        const token = Cookies.get('csrftoken');
        console.log('CSRF token fetched successfully:', token ? 'Token received' : 'No token set');
      } else {
        console.warn('Failed to fetch CSRF token:', response.status);
      }
    } else {
      console.log('CSRF token already exists:', Cookies.get('csrftoken') ? 'Token exists' : 'No token found');
    }
  } catch (error) {
    console.error('Error fetching CSRF token:', error);
    // Continue without CSRF token - backend will handle exempt views
  }
};

// Create axios instance with default config
const api = axios.create({
  baseURL: `${API_BASE_URL}/api`,
  headers: {
    'Content-Type': 'application/json',
  },
  withCredentials: true, // Enable sending cookies with requests
});

// Add request interceptor to include auth token and CSRF token
api.interceptors.request.use(
  async (config) => {
    // Fetch CSRF token if needed
    await fetchCSRFToken();
    
    const token = localStorage.getItem('accessToken');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    
    // Add CSRF token to headers
    const csrfToken = Cookies.get('csrftoken');
    if (csrfToken) {
      config.headers['X-CSRFToken'] = csrfToken;
    }
    
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// Add response interceptor to handle errors
api.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response?.status === 401) {
      // Handle unauthorized access
      localStorage.removeItem('accessToken');
      window.location.href = '/login';
    }
    return Promise.reject(error);
  }
);

